import React from "react"
import { appConfig } from '../../config/appConfig';
import { Link } from 'react-router-dom';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

export default class ProjectDetails extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/help">
                        Help Center
                    </Link>
                    <Link to="/help/projectDetails">
                        Edit Project
                    </Link>
                </Breadcrumbs>
                <h1>Managing a Project</h1>
                <p>
                    Navigate to this page by clicking the gear icon for a project in the Project Manager page.
                </p>
                <h3>Editing and previewing</h3>
                <p>
                    To enter Ezyvue Studio click the EDIT button below the model image.<br />
                    Preview the model as it will appear on your webpage by clicking the PREVIEW button.<br />
                    Click the DELETE button and confirm to delete the project permanently.
                    <br />
                    <br />
                    <em>NOTE: this operation cannot be undone</em>
                </p>
                <img alt='' src={require('assets/images/help/model.jpg')} />
                <h3>Project properties</h3>
                <img alt='product' src={require('assets/images/help/product.jpg')} />
                <h4>Name</h4>
                <p>
                    The name of your project which will appear in the info window pop-up in the viewer.
                </p>
                <h4>Description</h4>
                <p>
                    The description of your project which will appear in the info window pop-up in the viewer.
                </p>
                <h4>External Product ID</h4>
                <p>
                    For certain site builder integrations a product id is entered here. For more information see the integration guides.
                </p>
                <h4>Domain Locking URL</h4>
                <p>
                    To prevent the viewer iframe being embedded on another site, enter your site domain name.
                    This does not have to be the entire url string e.g. ezyvue.com.<br /><br />
                    Note: contact us if you are experiencing problems, for some sites the domain name required may be different from the actual domain name.
                </p>
                <h3>Asset FIles</h3>
                <img alt='' src={require('assets/images/help/assetfiles.jpg')} />
                <h4>Downloading files</h4>
                <p>
                    You can export a model in binary glTF (OpenGL Transmission Format) or USDZ (iOS Agumented Reality Format).
                    Download the model by clicking the download icon.
                </p>
                <h4>Re-uploading glTf file</h4>
                <p>
                    If you make external changes to a model it can be re-uploaded by clicking the upload icon. This will take you to the model upload page.<br /><br />
                    <em>
                        Note: if you re-upload a model it will replace the existing model and you will lose changes made to materials, position, scale or rotation. All other changes in the editor will be unaffected.
                    </em>
                </p>
                <h4>Rebuilding the USDZ file. </h4>
                <p>
                    You can re-convert the GLTF to USDZ after making changes to materials by clicking the refresh icon.
                </p>

                <h3>Settings files</h3>
                <p>
                    Backup/restore you settings files.
                </p>
                <h3>Uploaded Images</h3>
                <p>
                    View and download uploaded image files
                </p>
                <h3>Model Parts</h3>
                <img alt='product' src={require('assets/images/help/parts.jpg')} />
                <p>
                    Upload and manage 3d model part files for the Product Configurator.
                </p>
            </div>
        );
    }
}